import React from  'react';

export default function BoardWrite(props){

    const setbcategory = () => { alert(''); }
    const setboard     = () => { alert(''); }

    return(
        <div>
            <h1>    </h1>
              <input type="text" className="bcname" />
                <button type="button" onClick={ setbcategory }>  </button>
                <div name="bcategorybox"></div>

                <form className="boardform">
                     : <input type="text" name="btitle"/> <br/>
                     : <input type="text" name="bcontent"/> <br/>
                    : <input type="file"  name="bfile"/> <br/>
                    <button type="button" onClick={ setboard }>  </button>
                </form>
        </div>
    );
}